<template>
    <view class="page bgwhite">
        <view class="list">
            <u--form ref="uform" :model="form" :rules="rules" label-width="110px">
                <u-form-item :label="$t('工单号')" style="padding-left: 10px;" prop="orderNo">
                    <u--input v-model="form.orderNo" :placeholder="$t('please.input')" border="surround"
                        style="height: 20px;margin-right: 10px;"></u--input>
                </u-form-item>
                <u-form-item :label="$t('派工单号')" style="padding-left: 10px;" prop="dispatchNo">
                    <u--input v-model="form.dispatchNo" :placeholder="$t('please.input')" border="surround"
                        style="height: 20px;margin-right: 10px;"></u--input>
                </u-form-item>
                <u-form-item :label="$t('工作中心')" style="padding-left: 10px;" prop="wsCode">
                    <u--input v-model="form.wsCode" :placeholder="$t('please.input')" border="surround"
                        style="height: 20px;margin-right: 10px;"></u--input>
                </u-form-item>
                <u-form-item :label="$t('机器编号')" style="padding-left: 10px;" prop="machineNo">
                    <u--input v-model="form.machineNo" :placeholder="$t('please.input')" border="surround"
                        style="height: 20px;margin-right: 10px;"></u--input>
                </u-form-item>
                <u-form-item :label="$t('产品编号')" style="padding-left: 10px;" prop="itemNo">
                    <u--input v-model="form.itemNo" :placeholder="$t('please.input')" border="surround"
                        style="height: 20px;margin-right: 10px;"></u--input>
                </u-form-item>
                <u-form-item :label="$t('模具编号')" style="padding-left: 10px;" prop="mouldNo">
                    <u--input v-model="form.mouldNo" :placeholder="$t('please.input')" border="surround"
                        style="height: 20px;margin-right: 10px;"></u--input>
                </u-form-item>
                <u-form-item :label="$t('开始时间')" style="padding-left: 10px;" prop="palnStartTime">
                    <view class="itemView">
                        <u--input v-model="form.startDate" :placeholder="$t('please.select')" border="surround"
                            suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
                            @focus="startTimeShow = true"></u--input>
                        <u-datetime-picker :show="startTimeShow" v-model="valueDate" mode="datetime" :formatter="formatter"
                            @cancel="startTimeShow = false" @confirm="startTimeConfirm" @change="startTimeChangeHandler"
                            :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
                    </view>
                </u-form-item>
                <u-form-item :label="$t('结束时间')" style="padding-left: 10px;" prop="palnEndTime">
                    <view class="itemView">
                        <u--input v-model="form.endDate" :placeholder="$t('please.select')" border="surround"
                            suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
                            @focus="endTimeShow = true"></u--input>
                        <u-datetime-picker :show="endTimeShow" v-model="valueDate" mode="datetime" :formatter="formatter"
                            @cancel="endTimeShow = false" @confirm="endTimeConfirm" @change="endTimeChangeHandler"
                            :cancel-text="$t('取消')" :confirm-text="$t('确认')"></u-datetime-picker>
                    </view>
                </u-form-item>
                <u-form-item :label="$t('检查类型')" style="padding-left: 10px;" prop="checkStatus">
                    <view class="itemView"><u--input v-model="form.checkStatus" :placeholder="$t('please.select')"
                            border="surround" suffixIcon="arrow-down" suffixIconStyle="color: #909399" style="height: 20px;"
                            @focus="checkTypesShow = true"></u--input>
                        <u-picker :show="checkTypesShow" :columns="checkTypes" @cancel="checkTypesShow = false"
                            @confirm="checkTypesConfirm" @change="checkTypesChangeHandler" :cancel-text="$t('取消')"
                            :confirm-text="$t('确认')"></u-picker>
                    </view>
                </u-form-item>
            </u--form>
        </view>

        <u-button class="confirmBtn" shape="circle" :text="$t('screening')" @click="submit"></u-button>
    </view>
</template>

<script>
import pApi from "@/api/public/index.js"
export default {
    filters: {
        formatDate(time) {
            var data = new Date(time);
            return formatDate(data, 'yyyy MM dd hh:mm:ss');
        }
    },
    onLoad() {
        this.getCheckTypesStatus()
    },
    onShow() {
        uni.setNavigationBarTitle({ // 修改头部标题
            title: this.$t("bad.record.screening")
        });
    },
    data() {
        return {
            form: {
                orderNo: null,
                machineNo: null,
                mouldNo: null,
                itemNo: null,
                dispatchNo: null,
                wsCode: null,
                startDate: null,
                endDate: null,
                checkType: null,
                checkStatus: null
            },
            rules: {},
            finishShow: false,
            excessiveFinishShow: false,
            checkTypesShow: false,
            startTimeShow: false,
            endTimeShow: false,
            finishTimeShow: false,
            changeOrderTimeShow: false,
            selectFinishIndex: 0,
            selectExcessiveIndex: 0,
            selectStatusIndex: 0,
            startTime: null,
            endTime: null,
            valueDate: Number(new Date()),
            finishs: [
                ['是', '否']
            ],
            checkTypes: [],
            checkList: []
        };
    },
    methods: {
        //获取检查类型业务表
        getCheckTypesStatus() {
            uni.$u.http.get(pApi.checkType).then(
                result => {
                    console.log('result', result);
                    if (result.code === 200) {
                        this.checkList = result.data
                        let checkTypesList = []
                        result.data.forEach(item => {
                            checkTypesList.push(this.$t(item.name))
                        })
                        if (checkTypesList.length > 0) {
                            this.checkTypes = []
                            this.checkTypes.push(checkTypesList)
                        }
                    }
                }).catch(er => {
                    console.log('er', er);
                })
        },
        submit() {
            this.$refs.uform.validate(valid => {
                if (valid) {
                    console.log('验证通过');
                } else {
                    console.log('验证失败');
                }
            });

            uni.$emit('returnBadRecordData', this.form)
            uni.navigateBack({
                delta: 1
            });
        },
        checkTypesChangeHandler(e) {
            this.selectStatusIndex = e.index
        },
        checkTypesConfirm() {
            this.checkTypesShow = false
            this.form.checkType = this.checkList[this.selectStatusIndex].code
            this.form.checkStatus = this.checkList[this.selectStatusIndex].name
        },
        startTimeChangeHandler(e) {
            console.log("选择的时间=", e.value)
            this.startDate = e.value
        },
        startTimeConfirm() {
            this.startTimeShow = false
            this.form.startDate = this.timestampToTime(this.startDate ? this.startDate : this.valueDate)
            this.handleChangeUpdateTime()
        },
        endTimeChangeHandler(e) {
            console.log("选择的时间=", e.value)
            this.endDate = e.value
        },
        endTimeConfirm() {
            this.endTimeShow = false
            this.form.endDate = this.timestampToTime(this.endDate ? this.endDate : this.valueDate)
            this.handleChangeUpdateTime()
        },

        //时间戳转时间
        timestampToTime(value) {
            let date = new Date(value);
            let y = date.getFullYear(); // 年
            let MM = date.getMonth() + 1; // 月
            MM = MM < 10 ? ('0' + MM) : MM;
            let d = date.getDate(); // 日
            d = d < 10 ? ('0' + d) : d;
            let h = date.getHours(); // 时
            h = h < 10 ? ('0' + h) : h;
            let m = date.getMinutes(); // 分
            m = m < 10 ? ('0' + m) : m;
            let s = date.getSeconds(); // 秒
            s = s < 10 ? ('0' + s) : s;
            return y + '-' + MM + '-' + d + " " + h + ':' + m + ':' + s;
        },

        formatter(type, value) {
            if (type === 'year') {
                return `${value}年`
            }
            if (type === 'month') {
                return `${value}月`
            }
            if (type === 'day') {
                return `${value}日`
            }
            if (type === 'hour') {
                return `${value}时`
            }
            if (type === 'minute') {
                return `${value}分`
            }
            return value
        },

        handleChangeUpdateTime() {
            // 开始时间
            var start = (new Date(this.form.startDate) * 1000) / 1000;

            // 结束时间
            var end = (new Date(this.form.endDate) * 1000) / 1000;

            // 结束时间小于开始时间
            if (start !== 0 && end !== 0) {
                if (start > end) {
                    this.form.endDate = this.form.startDate;
                    uni.$u.toast("结束时间小于开始时间！")
                }
            }
        },

    }
}
</script>

<style lang="scss" scoped>
.list {
	
    &-item {
        padding: 10upx 16upx;
        @include after();
        font-size: 28upx;
        border: 0;
        justify-content: center;
        align-items: center;

        .label {
            width: 200upx;
            color: #333;
            font-weight: 500;
        }

        .value {
            color: #666;
        }
    }
}

.itemView {
    width: 100%;
    margin-right: 10px;
}

.confirmBtn {
    width: 80%;
    margin-top: 20px;
    margin-bottom: 20px;
    align-items: center;
    color: white;
    font-size: 20px;
    background-color: $ch-theme;
}
</style>